import Vue from 'vue'
import Router from 'vue-router'
//1.引入Test组件
import Home from "./components/tabbar/Home.vue"
// 引入文章列表页
import textList from "./components/tabbar/textList.vue"
// 引入文章详情页
import textDetails from "./components/tabbar/text_details.vue"
// 引入商品详情页
import ProDetails from "./components/tabbar/details.vue"
// 引入登陆页面
import Login from "./components/tabbar/login.vue"
// 引入购物车页面
import shopCart from "./components/tabbar/shopcart.vue"
// 引入个人中心页面
import person from "./components/tabbar/person.vue"
import p from "./components/tabbar/1.vue"
Vue.use(Router)
export default new Router({
  routes:[
    {path:'/',redirect:"/index"},
    {path:"/index",component:Home},
    {path:"/textlist",component:textList},
    {path:"/text_details",component:textDetails},
    {path:"/details",component:ProDetails},
    {path:"/login",component:Login},
    {path:"/shopcart",component:shopCart},
    {path:"/person",component:person},
    {path:"/p",component:p}
  ]
})



