<template>
    <div class="container" style="min-height:619px">
        <div>

        </div>
        <ul class="title" style="list-style:none;">
            <li :class="show==1?'show1':''" @click="change">账号密码登陆</li>
            <li :class="show==1?'':'show1'" @click="change">用户名注册</li>
        </ul>
        <!-- 登陆列表 -->
        <ul :class="show==1?'show':'hide'">
            <li>
                <input class="uname" v-model="uname" style="padding-left:15px;font-size:14px;" type="text" placeholder="用户名/邮箱地址">
            </li>
            <li>
                <input class="upwd" v-model="upwd" style="padding-left:15px;font-size:14px;"  type="password" placeholder="填写密码">
            </li>
            <li>
                <input type="text" v-model="login_warn" style="border:0; height:30px;margin:0;color:red;font-size:14px;">
            </li>
            <li>
                <mt-button style="width:100%;height:42px;background:#442818;color:#fff;border-radius:0; margin:10px 0 20px 0;" @click="login">登陆</mt-button>
            </li>
            
            <li>
                <div class="forget">
                    <p>
                        <span>忘记密码</span>
                        <span @click="change">去注册</span>
                    </p>
                </div>
            </li>
        </ul>
        <!-- 注册列表 -->
        <ul :class="show!=1?'show':'hide'">
            <!-- 填写注册用户名 -->
            <li>
                <input class="new_uname" @blur="search" v-model="new_uname" style="border:0;padding-left:15px;font-size:14px;border-bottom:2px solid #efefef;margin-bottom:8px" type="text" placeholder="用户名/邮箱">
            </li>
            <!-- 填写注册密码 -->
            <li>
                <input class="new_upwd" v-model="new_upwd" style="border:0;padding-left:15px;font-size:14px;border-bottom:2px solid #efefef;margin-bottom:8px" type="password" placeholder="密码">
            </li>
            <!-- 填写手机号 -->
            <li>
                <input class="phone" v-model="phone" style="border:0;padding-left:15px;font-size:14px;border-bottom:2px solid #efefef;margin-bottom:8px" type="text" placeholder="输入手机号">
            </li>
            <!-- 填写验证码 -->
            <li>
                <input type="text" style="border:0;padding-left:15px;font-size:22px;border-bottom:2px solid #efefef;text-align:center;width:50%;margin-bottom:8px;" v-model="test1"  placeholder="请输入图片字符">
                <span style="display:inline-block;width:48%;height:64px;padding:0;">
                    <p id="canvas" style="width:160px;height:50px;overflow:hidden;">
                        <span style="text-align:left;color:red;position:relative;" :style="where1">{{random1}}</span>
                        <span style="text-align:left;color:red;position:relative;" :style="where2">{{random2}}</span>
                        <span style="text-align:left;color:red;position:relative;" :style="where3">{{random3}}</span>
                        <span style="text-align:left;color:red;position:relative;" :style="where4">{{random4}}</span>
                    </p>
                    <button @click="test" style="display:inline-block;margin-left:50px;">换一张</button>
                </span>
            </li>
            <!-- 选择生日 -->
            <li style="margin-top:20px">
                <!-- for Vue 2.0 -->
                <div class="row">
                    <date-picker :date="startTime" :option="option" :limit="limit"></date-picker>
                </div>
            </li>
            <input type="text" style="border:0;height:30px;;color:red;margin:0;font-size:14px;" v-model="warn" disabled>
            <li>
                <mt-button style="width:100%;height:42px;background:#442818;color:#fff;border-radius:0; margin:10px 0 20px 0;" @click="reg">注册</mt-button>
            </li>
        </ul>
        	
    </div>
</template>
<script>
import myDatepicker from 'vue-datepicker/vue-datepicker-es6.vue'
import {Toast} from "mint-ui"
export default {
    data(){
        return {
            // 显示或隐藏注册框
            show:1,
            // 用户信息
            uname:"",
            upwd:"",
            new_uname:"",
            new_upwd:"",
            phone:"",
            // 验证码 
             random1:"1111",
             random2:"2",
             random3:"",
             random4:"",
             where1:{
                top:"25px",
                left:"32px",
                fontSize:"25px"},
            
             where2:{
                top:"25px",
                left:"32px",
                fontSize:"25px"},
            
             where3:{
                top:"25px",
                left:"32px",
                fontSize:"25px"},
            
             where4:{
                top:"25px",
                left:"32px",
                fontSize:"25px"},
            // 判断验证码
            text_char:"",
            test1:"",
            // 选择生日
             // for Vue 2.0
             startTime: {
                time: '' },
            
             endtime: {
                time: '' },
             option: {
                type: 'day',
                week: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'],
                month: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'],
                format: 'YYYY-MM-DD',
                placeholder: '请选择生日?',
                inputStyle: {
                    'display': 'inline-block',
                    'width': '100%',
                    'padding': '6px',
                    'padding-left': '16px',
                    'line-height': '22px',
                    'font-size': '16px',
                    'border': '0',
                    'border-bottom':'2px solid #efefef',
                    'border-radius': '2px',
                    'color': '#5F5F5F'
                },
                color: {
                    header: '#1b98ee',
                    headerText: '#fff'
                },
                buttons: {
                    ok: '确定',
                    cancel: '取消'
                },
                overlayOpacity: 0.5,
                dismissible: true},
             timeoption: {
             type: 'min',
             week: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'],
             month: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'],
             format: 'YYYY-MM-DD HH:mm'
             },
             multiOption: {
             type: 'multi-day',
             week: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'],
             month: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'],
             format: 'YYYY-MM-DD HH:mm'
             },
             limit: [{
             type: Array,
              default: function _default () {
                return []
              }
             }],
            birth:"", //即 startTime 
            warn:"", //注册提示框
            login_warn:"" //登陆提示框
        } 
    },
    created(){
        this.test()
        console.log()
    },
    methods:{
        // 用户名验证是否存在
        search(){
            var url="search?uname="+this.new_uname;
            this.axios.get(url).then(res=>{
                if(res.data.code==1){
                    Toast(res.data.data);
                    this.new_uname=""
                }
            })
        },
        // 注册框
        reg(){
            if(!this.new_uname){
                this.warn="用户名不能为空";
                return;
            }
            this.warn=""
            if(!this.new_upwd){
                this.warn="密码不能为空";
                return;
            }
            this.warn=""
            if(!this.phone){
                this.warn="手机号不能为空";
                return;
            }
            this.warn=""
            if(this.text_char!=this.test1){
                this.warn="验证码不正确";
                this.test();
                this.test1=""
                return;
            }
            this.warn=""
            if(!this.startTime.time){
                this.warn="生日不能为空";
                return;
            }
            this.warn=""
            var postDate=this.qs.stringify({
                uname:this.new_uname,
                upwd:this.new_upwd,
                phone:this.phone,
                birth:this.startTime.time
            });  
            // console.log(postDate)
            var url="reg";
            this.axios.post(url,postDate).then(res=>{
                Toast(res.data.data);
            });
        },
        // 登陆框
        login(){
            if(!this.uname){
                this.login_warn="用户名不能为空！"
                return;
            }
            if(!this.upwd){
                this.login_warn="密码不能为空！"
                return;
            }
            var postDate=this.qs.stringify({
                uname:this.uname,
                upwd:this.upwd
            });
            var url="login";
            this.axios.post(url,postDate).then(res=>{
                if(res.data.code==1){
                    // 将用户的id存储到Vuex中
                    sessionStorage.setItem("uid",res.data.data[0].id)
                    Toast("登陆成功,1秒后跳转到主面");
                    setTimeout(()=>{
                       this.$router.push("/index")
                    },2000)
                }else{
                    Toast(res.data.data);
                    this.uname=""
                    this.upwd=""
                }
            });
        },
        // 注册登陆框切换
        change(){
           if(this.show==1){
               this.show=0;
           }else{
               this.show=1;
           }
        },
        //验证码 实现
        test(){
           this.text_char=""
            var random1=Math.floor(Math.random()*5);
            this.random1=random1;
            this.text_char+=random1;
            var random2=Math.floor(Math.random()*5);
            this.random2=random2;
            this.text_char+=random2;
            var random3=Math.floor(Math.random()*5);
            this.random3=random3;
            this.text_char+=random3;
            var random4=Math.floor(Math.random()*5);
            this.random4=random4;
            this.text_char+=random4;
            var random_top1=Math.floor(Math.random()*30);
            var random_left1=Math.floor(Math.random()*25);
            this.where1.top=random_top1+"px";
            this.where1.left=random_left1+"px";
            this.where1.fontSize=random_top1+"px";
            var random_top2=Math.floor(Math.random()*30);
            var random_left2=Math.floor(Math.random()*25);
            this.where2.top=random_top2+"px";
            this.where2.left=random_left2+30+"px";
            this.where2.fontSize=random_top2+"px";
            var random_top3=Math.floor(Math.random()*30);
            var random_left3=Math.floor(Math.random()*25);
            this.where3.top=random_top3+"px";
            this.where3.left=random_left3+60+"px";
            this.where3.fontSize=random_top3+"px";
            var random_top4=Math.floor(Math.random()*30);
            var random_left4=Math.floor(Math.random()*25);
            this.where4.top=random_top4+"px";
            this.where4.left=random_left4+90+"px";
            this.where4.fontSize=random_top4+"px";
        }
    },
    watch:{
        uname(){
            this.login_warn=""
        },
        upwd(){
            this.login_warn=""
        }
    },
    components: {
       'date-picker': myDatepicker
    }
}
</script>
<style>

.new_uname{
    padding-right:30px !important;
}
.container{
    padding: 0 28px;
}
ul{
    padding: 0; 
    list-style: none;
}
.title{
    display: flex;
    justify-content: space-between;
    
    border-bottom: 1px solid rgba(0,0,0,.1);
}
.title li{
    width: 50%;
    text-align: center;
    padding: 8px 0;
    line-height: 20px;
    text-align: center;
    font-size: 14px;
    color: #969696;
    
}
.show1{
    color: #333 !important;
    border-bottom: 1px solid #333;
}
.uname,.upwd{
    border: 0 !important;
    border-bottom: 2px solid #efefef !important;
}
input::-webkit-input-placeholder {
    /* placeholder颜色  */
    color: #d5bfa7;
    /* placeholder字体大小  */
    font-size: 14px;
    /* placeholder位置  */
    text-align: left;
}
.forget{
    display: flex;
    justify-content: flex-end;
}
.forget p span{
    margin: 0 10px;
    color: #442818;
    font-size: 13px;
}
</style>




