import Vue from 'vue'
import App from './App.vue'
import router from './router'
import axios from 'axios' 
import'mint-ui/lib/style.css'

Vue.config.productionTip = false
// 0.1:引入Vuex组件
import Vuex from "vuex"
// 0.2：注册Vuex组件
Vue.use(Vuex)
// 0.3：创建store对象
var store=new Vuex.Store({
  // 共享数据容器
  state:{
    cartCount:sessionStorage.getItem("cartCount")||0,
    uid:sessionStorage.getItem("uid")||''
    // product_sum:0
  },
  //操作共享数据方法
  mutations:{
    increment(state){
      state.cartCount++;
      sessionStorage.setItem("cartCount",state.cartCount)
    }
  },
  //获取共享数据的方法
  getters:{
    optCartCount:function(state){
      return state.cartCount;
    }
  }
  // 获取
  // {{$store.getters.optCartCount}}
  // 设值
  // this.c1=this.$store.state.fj1;
})
// 4：将store绑定Vuex实例
axios.defaults.withCredentials=true;
Vue.prototype.axios=axios;
//5: 设置请求的根路径 
//6:全局设置post 时候表音的数据组织格式为 application/x-www-form-urlencoded
import './lib/mui/css/mui.css'
// 导入 MUI 的样式表，扩展图标样式，购物车图标
// 还需要加载图标字体文件
import './lib/mui/css/icons-extra.css'

// 1、引入header组件，
import { Header,Button,Search,Swipe,SwipeItem, Popup } from "mint-ui"
Vue.component(Popup.name, Popup);
// 1.1、注册header组件
Vue.component(Header.name,Header);
//2、引入button组件
//2.1、注册button组件
Vue.component(Button.name,Button);
//3、引入搜索组件
//3.1、注册search组件
Vue.component(Search.name,Search)
//4、引入轮播父组件
//4.1、注册Swipe组件
Vue.component(Swipe.name,Swipe)
//5、引入轮播子组件
//5.1、注册SwipeItem组件
Vue.component(SwipeItem.name,SwipeItem);

//6.1、注册过滤器
Vue.filter("dateFilter",function(value){
    var now=new Date(value);
    var y=now.getFullYear();
    var m=now.getMonth()+1;
    var d=now.getDate();
    var h=now.getHours();
    var mi=now.getMinutes();
    var s=now.getSeconds();
    if(m<10){
      m="0"+m;
    }
    if(d<10){
      d="0"+d;
    }
    if(h<10){
      h="0"+h;
    }
    if(mi<10){
      mi="0"+mi;
    }
    if(s<10){
      s="0"+s;
    }
  return `${y}-${m}-${d} ${h}:${mi}:${s}`
  // .toLocaleString()
})

//7.main.js  引入第三方库  qs
//解决axios post请求问题
import qs from "qs";
Vue.prototype.qs=qs;
new Vue({
  router,
  render: h => h(App),
  store   //0.4 将store绑定Vue实例
}).$mount('#app')



