<template>
    <div>
        <ul style="list-style:none;padding:0">
            <li v-for="(elem,i) of textList" :key="i">
                <p class="top_time">{{elem.text_time}}</p>
                <div class="mui-card">
                    <div class="mui-card-header mui-card-media">
                        <div class="mui-media-body" style="margin:10px 0 10px 0;font-size:17px;">
                            {{elem.text_title}}
                            <p style="font-size:13px;margin:8px 0 10px 0;">2016-06-30</p>
                        </div> 
                        <router-link  :to="{path:'/text_details',query:{pid:elem.pid}}"><img :src="elem.text_img" width="100%" alt=""></router-link>
                        <h4>&nbsp;</h4>
                    </div>
                    <div class="mui-card-footer">
                        <router-link :to="{path:'/text_details',query:{pid:elem.pid}}" class="mui-card-link" style="color:#040404;font-size:13px;">阅读全文</router-link>
                        <router-link :to="{path:'/text_details',query:{pid:elem.pid}}" class="mui-card-link" style="color:#040404;font-size:23px;">></router-link>
                    </div>
                </div>
            </li>
        </ul>
    </div>
</template>
<script>
export default {
    data(){
        return {
            textList:[],
            pno:1
        }
    },
    created(){
    // 页面加载完成自动加载4条数据
        this.text_list();
    // 划到底部自动加载更多 
        window.onscroll = ()=>{
        // 此处只能用箭头函数，因为this需要指向的是Vue对象，不用箭头函数，this将指向window
            //变量scrollTop是滚动条滚动时，距离顶部的距离
            var scrollTop = document.documentElement.scrollTop||document.body.scrollTop;
            //变量windowHeight是可视区的高度
            var windowHeight = document.documentElement.clientHeight || document.body.clientHeight;
            //变量scrollHeight是滚动条的总高度
            var scrollHeight = document.documentElement.scrollHeight||document.body.scrollHeight;
            //滚动条到底部的条件
            if(scrollTop+windowHeight==scrollHeight){
            //写后台加载数据的函数
                this.pno++;
                var url="text_list?pno="+this.pno;
                this.axios.get(url).then(result=>{
                    var rows=this.textList.concat(result.data)
                    this.textList=rows;
                });
            } 
        }
    },
    methods:{
         // 页面加载完成自动加载4条数据
        text_list(){
            var url="text_list";
            this.axios.get(url).then(result=>{
                this.textList=result.data;
            })
        },
    }
}
</script>
<style>
.top_time{
    background:#cecece;
    text-align:center;
    width:73px;
    border-radius:3px;
    font-size: 12px;
    margin: 0 auto;
    color: #fff;
}
</style>



