<template>
    <div class="app-detais">
        <!-- 轮播 -->
        <div>
            <mt-swipe :auto="30000"  style="height:353px">
                <mt-swipe-item>
                    <img src="img/new1_1.jpg" width="100%" alt="">
                </mt-swipe-item>
                <mt-swipe-item>
                    <img src="img/new1_2.jpg" width="100%" alt="">
                </mt-swipe-item>
                <mt-swipe-item>
                    <img src="img/new1_3.jpg" width="100%" alt="">
                </mt-swipe-item>
            </mt-swipe>
        </div>
        <!-- 商品详情信息 -->
        <div class="details_box" style="padding:;">
            <!-- 商品名称介绍 -->
            <div style="padding-left:24px; margin-top:10px;">
                <p style="font-size:24px; color:#333;padding-top:10px;margin:0;">{{title_en}}</p>
                <p style="color:#000;margin-bottom:5px;">{{title_cn}}</p>
            </div>
            <!-- 商品价格介绍 -->
            <p style="padding-left:24px; margin-bottom:5px">
                <span style="display:inline-block;color:#c69c6D;font-size:23px;height:30px;
                line-height:30px;">￥{{price}}</span>
            </p>
            <span style="padding-left:24px;margin:5px 0;display:block;font-size:12px;color:#969696">{{introduce}}</span>
            <!-- 商品类型 -->
            <div class="details_type" style="padding-left:24px">
                <router-link v-for="(item,index) of details_type" :key="index" :to="{path:item.details_href}">{{item.details_type}}></router-link>
            </div>
            <!-- 商品规格 -->
            <div class="product_size" style="margin:0 20px;">
                <!-- 重量 -->
                <ul class="details_weight" @click="upward">
                    <li style="padding-right:20px;">
                        <p style="margin:0;color:#442818;">已选择：<span>{{selected_weight}}</span></p>
                        <span style="color:#969696 padding:15px; font-weight:700">></span>
                    </li>
                </ul>
                <mt-popup v-model="popupVisible" class="upward"  position="bottom">
                    <!-- 重量1详情 -->
                    <div v-for="(item,index) of information" :key="index" :class="index==0?'show':'hide'" :id="'div'+index">
                        <!-- 商品样式大小 -->
                        <div class="details_price">
                            <p style="font-size:18px;color:#442818; font-weight:700;">￥<span>{{item.price}}</span></p>
                            <p @click="close" style="font-size:25px;color:#442818;">×</p>
                        </div>
                        <!-- 商品配件 -->
                        <div class="details_price">
                            <img :src="item.img" width="55%" height="95px" alt="">
                            <div class="details_introduce">
                                <p style="width:200px">{{item.adpt}}</p>
                                <p style="width:200px">{{item.contain}}</p>
                                <p style="width:200px">{{item.size}}</p>
                                <p style="width:200px">{{item.timer}}</p>
                            </div>
                        </div>
                    </div>
                    <!-- 商品规格 -->
                    <p style="padding:0;margin:5px;">商品规格</p>
                    <div class="product_weight">
                        <p v-for="(item,index) of weight" :key="index" @click="select(index)" :class="activeClass==index?'sel':''">{{item.title}}</p>
                    </div>
                    <div style="margin-top:20px;height:40px;">
                    </div>
                </mt-popup>
                
            <!-- 尺寸 -->
                <ul class="details_size">
                    <li style="margin-bottom: 5px;height:35px;">
                        <i><img src="img/size.png" alt=""></i>
                        {{details_li.adpt}}
                    </li>
                    <li style="margin-bottom: 5px;">
                        <i><img src="img/laid.png" alt=""></i>
                        {{details_li.contain}}
                    </li>
                    <li>
                        <i><img src="img/unmber.png" alt=""></i>
                        {{details_li.size}}
                    </li> 
                    <li>
                        <i><img src="img/time.png" alt=""></i>
                        {{details_li.timer}}
                    </li>
                </ul>
                <!-- 保险条件 -->
                <ul class="details_condition"> 
                    <li>
                        <img src="img/fresh.png" alt="">
                        <span>
                            保险条件：0-4°C保藏10小时，5°C食用为佳
                        </span>
                    </li>
                    <li>
                        <img src="img/sweet-02.png" alt="">
                        <span>
                            参考甜度：
                            <img :src="details_img.sweet1" alt="">
                            <img :src="details_img.sweet2" alt="">
                            <img :src="details_img.sweet3" alt="">
                            <img :src="details_img.sweet4" alt="">
                            <img :src="details_img.sweet5" alt="">
                        </span>
                    </li>
                    
                </ul>
            </div>
        </div>
        <!-- 添加购物车 -->
        <div class="footer1" style="width:100%;">
            <mt-button style="width:50%; height:50px;box-sizing:border-box;" @click="addcartGo">立即购买</mt-button>
            <mt-button @click="addcart" style="background:#442818;color:#fff;width:50%; height:50px;box-sizing:border-box;">加入购物车</mt-button>
         </div>
    </div>
</template>
<script>
import {Toast} from "mint-ui"
export default {
    data(){
        return {
            uid:0,
            pid:1,
            title_en:"Cocoa Island",
            title_cn:"可可岛",
            price:"458.00",
            introduce:"精品巧克力蛋糕",
            // 商品的类型分类
            details_type:[],
            // 商品框内数据
            details_li:{},
            // 商品的甜度图片
            details_img:{},
            // 商品的已选择重量
            selected_weight:"2.0磅",
            // 是否显示下弹出框
            popupVisible:false,
            // 重量（磅数）列表
            weight:[
                {id:1,pid:1,title:"1.0磅"},
                {id:2,pid:1,title:"2.0磅"},
                {id:3,pid:1,title:"3.0磅"},
                {id:4,pid:1,title:"5.0磅"}
            ],
            // 是否添加sel
            activeClass:0,
            // 上拉数据内容
            information:[
                {id:1,pid:1,price:"199.00",img:"img/sm1.jpg",adpt:"13.5*13.5",contain:"含10套餐具(蜡烛需单独订购)",size:"适合3-4人分享",timer:"最早明天07:30配送"},
                {id:2,pid:1,price:"289.00",img:"img/md2.jpg",adpt:"15.5*15.5",contain:"含10套餐具(蜡烛需单独订购)",size:"适合7-8人分享",timer:"最早明天10:30配送"},
                {id:3,pid:1,price:"399.00",img:"img/md3.jpg",adpt:"17.5*17.5",contain:"含10套餐具(蜡烛需单独订购)",size:"适合10-13人分享",timer:"最早明天09:30配送"},
                {id:4,pid:1,price:"499.00",img:"img/lg5.jpg",adpt:"22.5*22.5",contain:"含10套餐具(蜡烛需单独订购)",size:"适合16-17人分享",timer:"最早明天08:30配送"}
            ],
        }
    },
    created(){   
        this.uid=this.$store.state.uid;
        // 商品id
        this.pid=document.URL.split("=")[1];
        // 上拉数据内容
        this.details_box();
        // 上拉数据重量
        this.details_box_weight()
        // 商品的标题
        this.details_introduce();
        // 商品分类
        this.details_type1();
        // 商品甜度
        this.details_sweet();
        // 商品详情信息框 数据
        this.details_li=this.information[1]
        
    },
    methods:{
        // 商品上拉框 数据
        details_box(){
            var url="details_box?pid="+this.pid;
            this.axios.get(url).then(result=>{
                this.information=result.data.data
            })
        },
        // 商品上拉框 重量
        details_box_weight(){
            var url="details_box_weight?pid="+this.pid;
            this.axios.get(url).then(result=>{
                this.weight=result.data.data;
            })
        },
        // 商品的标题，价格，介绍
        details_introduce(){
            var url="details_introduce?pid="+this.pid;
            this.axios.get(url).then(result=>{
                var res=result.data.data;
                this.title_en=res.title_en;
                this.title_cn=res.title_cn; 
                this.price=res.price+".00";
                this.introduce=res.introduce;
                this.pid=res.pid;
                this.selected_weight=res.selected_weight;
            })
        },
        // 商品的分类 标签
        details_type1(){
            var url="details_type?pid="+this.pid;
            this.axios.get(url).then(result=>{
               this.details_type=result.data.data;
            })
        },
        // 商品的甜度
        details_sweet(){
            var url="details_sweet?pid="+this.pid;
            this.axios.get(url).then(result=>{
            this.details_img=result.data.data[0]
            })
        },
        // 点击商品磅数，动态切换class类名
        select(index){
            this.activeClass=index;
            var id="div"+index;
            this.details_li=this.information[index];
            document.querySelector(".show").className="hide";
            document.getElementById(id).removeAttribute("class","hide");
            document.getElementById(id).className="show";
            // 更改对应的价钱和重量
            this.price=document.querySelector(".show span").innerHTML
            this.selected_weight=document.getElementsByClassName("sel")[0].innerHTML

        },
        // 上拉下拉切换
        upward(){
            if(this.popupVisible){
                this.popupVisible=false
            }else{
                this.popupVisible=true
            }
        },
        close(){
            if(this.popupVisible){
                this.popupVisible=false
            }
        },
        addcart(e){
            // 动态获取商品重量和价格
            if(this.popupVisible){
                // 加入购物车
                if(!this.uid){
                    Toast("您还未登录,请先登陆");
                    this.$store.state.cartCount=0;
                    return;
                }else{
                    // 首先获取用户的编号 uid
                    var pname=this.title_cn;
                    var pid=this.pid;
                    var price=document.querySelector(".show span").innerHTML;
                    var weight=document.getElementsByClassName("sel")[0].innerHTML;
                    var url="addcart?uid="+this.uid+"&pid="+pid+"&pname="+pname+"&price="+price+"&weight="+weight;
                    this.axios.get(url).then(res=>{
                        this.$store.commit("increment");
                        Toast("添加购物车成功！")
                    })
                }
                
            }else{
                this.popupVisible=true;
            }
            
        },
        addcartGo(){
             // 动态获取商品重量和价格
            if(this.popupVisible){
                // 加入购物车
                if(!this.uid){
                    Toast("您还未登录,请先登陆");
                    this.$store.state.cartCount=0;
                    return;
                }else{
                    // 首先获取用户的编号 uid
                    var pname=this.title_cn;
                    var pid=this.pid;
                    var price=document.querySelector(".show span").innerHTML;
                    var weight=document.getElementsByClassName("sel")[0].innerHTML;
                    var url="addcart?uid="+this.uid+"&pid="+pid+"&pname="+pname+"&price="+price+"&weight="+weight;
                    this.axios.get(url).then(res=>{
                        this.$store.commit("increment");
                        this.$router.push("/shopcart");
                    })
                } 
            }else{
                this.popupVisible=true;
            }
        }
    }
}
</script>
<style>
.product_size{
    border: 1px solid #ddd;
    border-radius: 5px;
    margin-top: 20px !important;
    margin-bottom:20px !important;  
    box-shadow: 1px 3px 10px #ddd;
}
.details_type a{
    display: inline-block !important;
    line-height: 18px !important;
    color: #D5BFA7 !important;
    padding: 0 12px !important;
    border: 1px solid #D5BFA7 !important;
    border-radius: 100px !important;
    margin-right: 6px !important;
    text-decoration: none;
    cursor: pointer;
    font-size: 12px;
}
.details_weight{
    list-style:none;
    height:45px;
    padding-left: 15px;
    margin: 0;
    line-height:45px;
    background:#efeae4;
    border-radius: 8px 8px 0 0;
    color: #442818;
    margin-bottom:0px !important;
}
.details_weight>li{
    display:flex;
    justify-content: space-between;
    align-items: center;
}
.details_size{
    list-style:  none;
    margin: 0;
    padding: 0!important;
    display: flex;
    flex-wrap: wrap;
    padding-left: 15px !important;
}
.details_size li{
    width: 50%;
    font-size: 12px;
    height: 40px;
    line-height: 10px;
    display: flex;
    align-items: center;
    color:#482618;
    
}
.details_size li i img{
    width: 24px;
    height: 24px;
    margin-right: 15px;
}
.details_condition{
    padding: 0;
    list-style: none;
    font-size: 13px;
    color: #482618; 
    margin: 0; 
    margin-bottom:10px; 
    padding-left: 15px !important;
}
.details_condition li{
    margin: 0 auto;
    height: 36px;
    line-height: 36px;
}
.details_condition li:nth-child(1){
    border-top: 1px solid #d6d6d6;
}
.details_condition img{
    width: 20px;
    height: 20px;
    margin-right: 20px; 
    margin-top:10px; 
}
.upward{
    width: 100%;
    padding: 14px 24px 20px 24px;
}
.details_price{
    display: flex;
    justify-content: space-between;
}
.details_introduce{
    overflow: hidden;
}
.details_introduce p{
    font-size: 5px !important;
    margin-bottom: 3px;
    padding-left: 23px;
    color: #442818;
}
.details_introduce p:nth-child(1){
    /* background: url(img/size.png) no-repeat 5px 2px; */
    background-size: 15px 15px;
}
.details_introduce p:nth-child(2){
    /* background: url(img/laid.png) no-repeat 5px 2px; */
    background-size: 15px 15px;
}
.details_introduce p:nth-child(3){
    /* background: url(img/unmber.png) no-repeat 5px 2px; */
    background-size: 15px 15px;
}
.details_introduce p:nth-child(4){
    /* background: url(img/time.png) no-repeat 5px 2px; */
    background-size: 15px 15px;
}
.product_weight {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}
.product_weight>p{
    color: #442818;
    width: 21%;
    text-align: center;
    position: relative;
}
.sel{
    border: 1px solid #000;
}
 .sel::before{
    content: "";
    position: absolute;
    left: -1px;
    top: -1px;
    z-index: 10;
    width: 12px;
    height: 9px;
    /* background: url(img/icon_selected.png) no-repeat; */
    background-size:12px 9px;
}
.hide{
    display: none
}
.show{
    display: block
}
.footer1{
    position: fixed;
    display: flex;
    bottom: 0;
    z-index: 1000000;

}
</style>



