<template>
    <div class="app-text">
        <!-- 文章名称 -->
        <div style="padding:10px;">
            <p style="color:#442818;font-size:24px; padding-top:10px;">{{text_title}}</p>
            <p style="font-size:13px;">发布时间:{{text_time}}</p>
        </div>
        <div class="text_img" style="padding:0 15px 0 15px;">
            <img :src="text_img.text_img1" width="100%" alt="">
            <img :src="text_img.text_img2" width="100%" alt="">
            <img :src="text_img.text_img3" width="100%" alt="">
            <img :src="text_img.text_img4" width="100%" alt="">
            <img :src="text_img.text_img5" width="100%" alt="">
            <img :src="text_img.text_img6" width="100%" alt="">
            <img :src="text_img.text_img7" width="100%" alt="">
            <img :src="text_img.text_img8" width="100%" alt="">
            <img :src="text_img.text_img9" width="100%" alt="">
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            pid:0,
            text_title:"",
            text_time:"",
            text_img:{}
        }
    },
    created(){
        this.pid=document.URL.split("=")[1];
        this.text_details_title();
        this.text_details_img()
    },
    methods:{
        // 文章时间 标题
        text_details_title(){
            var url="text_details_time?pid="+this.pid;
            this.axios.get(url).then(result=>{
                this.text_time=result.data.data[0].text_time;
                this.text_title=result.data.data[0].text_title;
            });
        },
         // 文章 图片列表
        text_details_img(){
            var url="text_details_img?pid="+this.pid;
            this.axios.get(url).then(result=>{
                this.text_img=result.data.data[0];
                console.log(this.text_img)
            })
        }
    }
}
</script>
<style>
.app-text{
    background: #fff;
}
</style>



