<template>
    <div class="app-home">
        <!-- 轮播图  mint-ui-->
            <mt-swipe :auto="3000">
                <mt-swipe-item v-for="(img,i) of banner_img" :key="i">
                    <img :src="img.img_url" alt="" :data-id="img.bid">
                </mt-swipe-item>
            </mt-swipe>
        <!-- 活动条 -->
            <div class="activity mycolor myfont">
                <span>新人首单增小切块</span>
                <span>满100包邮</span>
                <span>全程冷链配送</span>
            </div>
        <!-- 九宫格  mui-->
            <ul class="mui-table-view mui-grid-view mui-grid-9">
                <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3 jiugongge" v-for="(elem,i) of four_list" :key="i">
                    <a href="#">
                    <img alt="" :src="elem.img_url" style="border-radius:50%">
                    <div class="mui-media-body">{{elem.title}}</div>
                    </a>
                </li>
            </ul>
        <!-- 新品 -->
            <div class="index_new">
                <p>新品</p>
                <!-- 点击更过，跳转当前类型商品分页类页 -->
                <p><a href="javascript:;">更多></a></p>
            </div>
            <!--新品 商品 -->
            <div class="mui-card" style="margin-bottom:30px;" v-for="(elem,i) of product_new" :key="i">
                <!-- 点击图片，获取商品pid，跳转详情页 -->
                <div class="mui-card-header mui-card-media" :data-pid="elem.pid" @click="details_url" style="height:50vw;" :style="{backgroundImage:'url(' + elem.new_url+')'}"></div>
                
                <div class="mui-card-content">
                    <div class="mui-card-content-inner">
                        <p class="pro_detail">
                            <span style="color:#322418;font-size:15px;margin-right:8px;font-weight:bold;">{{elem.pro_name}}</span>
                            <span style="color:#C69C6D;font-size:15px;display:inline-block;margin-top:-4px;vertical-align: middle;">{{elem.pro_price}}</span>
                            <span>元/{{elem.pro_type}}</span>
                        </p>
                        <p style="color: #442818;margin:0;">{{elem.pro_details}}</p>
                    </div>
                    <!-- 点击购物车，获取商品编号pid，添加至当前用户购物车 -->
                    <div class="cart" :data-pid="elem.pid" :data-pname="elem.pro_name" :data-price="elem.pro_price" :data-weight="elem.pro_type" @click="addCart" style="background-image:url(img/new-home-icon.png);background-position: -35px 3px;background-size: 80px auto">
                    </div>
                </div>
            </div>
        <!-- 人气 -->
            <div class="index_new">
                <p>人气</p>
                <!-- 点击更过，跳转当前类型商品分页类页 -->
                <p><a href="javascript:;">更多></a></p>
            </div>
            <!-- 人气 商品 -->
            <div class="mui-card" style="margin-bottom:30px;" v-for="(elem,i) of product_popular" :key="i">
                <!-- 点击图片，获取商品pid，跳转详情页 -->
                <div class="mui-card-header mui-card-media" :data-pid="elem.pid" @click="details_url" style="height:50vw;" :style="{backgroundImage:'url(' + elem.new_url+')'}"></div>
                
                <div class="mui-card-content">
                    <div class="mui-card-content-inner">
                        <p class="pro_detail">
                            <span style="color:#322418;font-size:15px;margin-right:8px;font-weight:bold;">{{elem.pro_name}}</span>
                            <span style="color:#C69C6D;font-size:15px;display:inline-block;margin-top:-4px;vertical-align: middle;">{{elem.pro_price}}</span>
                            <span>元/{{elem.pro_type}}</span>
                        </p>
                        <p style="color: #442818;margin:0;">{{elem.pro_details}}</p>
                    </div>
                    <!-- 点击购物车，获取商品编号pid，添加至当前用户购物车 -->
                    <div class="cart" :data-pid="elem.pid" :data-pname="elem.pro_name" :data-price="elem.pro_price" :data-weight="elem.pro_type" @click="addCart" style="background-image:url(img/new-home-icon.png);background-position: -35px 3px;background-size: 80px auto">
                    </div>
                </div>
            </div>
        <!-- 生日 -->
            <div class="index_new">
                <p>生日</p>
                <!-- 点击更过，跳转当前类型商品分页类页 -->
                <p><a href="javascript:;">更多></a></p>
            </div>
            <!-- 生日 商品 -->
            <div class="mui-card" style="margin-bottom:30px;" v-for="(elem,i) of product_birth" :key="i">
                <!-- 点击图片，获取商品pid，跳转详情页 -->
                <div class="mui-card-header mui-card-media" :data-pid="elem.pid" @click="details_url" style="height:50vw;" :style="{backgroundImage:'url(' + elem.new_url+')'}"></div>
                
                <div class="mui-card-content">
                    <div class="mui-card-content-inner">
                        <p class="pro_detail">
                            <span style="color:#322418;font-size:15px;margin-right:8px;font-weight:bold;">{{elem.pro_name}}</span>
                            <span style="color:#C69C6D;font-size:15px;display:inline-block;margin-top:-4px;vertical-align: middle;">{{elem.pro_price}}</span>
                            <span>元/{{elem.pro_type}}</span>
                        </p>
                        <p style="color: #442818;margin:0;">{{elem.pro_details}}</p>
                    </div>
                    <!-- 点击购物车，获取商品编号pid，添加至当前用户购物车 -->
                    <div class="cart" :data-pid="elem.pid" :data-pname="elem.pro_name" :data-price="elem.pro_price" :data-weight="elem.pro_type" @click="addCart" style="background-image:url(img/new-home-icon.png);background-position: -35px 3px;background-size: 80px auto">
                    </div>
                </div>
            </div>
        <!-- 甘一客·活动 -->
            <div class="index_act">
                <p>甘一客·活动</p>
            </div>
            <!-- 新客权益 -->
            <div class="index_activity" style="margin-bottom:30px;">
                <div style="width:40%; border:1px solid #dadada;border-right:0;box-shadow:0 1px 2px rgba(0, 0, 0, .3);margin-left:10px; padding-left:10px;">
                    <h6 style="">新客权益</h6>
                    <p>点击查看</p>
                    <span>只剩273天</span>
                </div>
                <div style="width:60%; border:1px solid #dadada;border-left:0;box-shadow:0 1px 2px rgba(0, 0, 0, .3);margin-right:10px;">
                    <img src="img/index_activity.jpg" width="100%" height="100%" alt="">
                </div>
            </div>
        <!-- 甘一客·文章 -->
            <div class="index_act">
                <p>甘一客·文章</p>
            </div>
            <!-- 文章列表 -->
            <div class="text" @touchmove="mousemove">
                <ul id="ul">
                    <li style="width:231px" v-for="(elem,i) of product_text" :key="i" :data-pid="elem.pid" @click="text_details">
                        <router-link :to="{path:'/text_details',query:{pid:elem.pid}}"><img :src="elem.text_img" :data-pid="elem.pid"  width="100%" alt=""></router-link>
                        <router-link :to="{path:'/text_details',query:{pid:elem.pid}}"><p style="padding-top:20px;padding-left:10px;color:#000;" :data-pid="elem.pid">{{elem.text_title}}</p></router-link>
                    </li>
                    <li style="width:130px" class="text_more"><router-link to="/textlist">查看更多</router-link></li>
                </ul>
            </div>
    </div>
</template>
<script>
import {Toast} from "mint-ui"
export default {
    data(){
        return{
            // 用户编号 uid
            uid:"",
            // 轮播图
            banner_img:[],
            // 九宫格
            four_list:[],
            // 新品列表
            product_new:[],
             // 人气列表
            product_popular:[],
            // 生日列表
            product_birth:[],
            // 文章列表
            product_text:[],
            marginLeft:0,
            touchsmoveX:0,
            x:0
        }
    },
    created(){
        this.uid=this.$store.state.uid;
        this.productList();
        this.banner();
        this.four();
        this.index_new();
        this.index_popular();
        this.index_birth();
        this.index_text();
    },
    methods:{
        // 判断用户是否登陆，如果登陆就将用户商品列表查出来放入公共数据中
        productList(){
            if(this.uid!=0){
                var url="productList?uid="+this.uid;
                this.axios.get(url).then(result=>{
                    var sum=0;
                    for(var res of result.data.data){
                       sum+=res.count;
                    }
                    this.$store.state.cartCount=sum;
                })
            }else{
                    this.$store.state.cartCount=0;
            }
        },
        // 文章列表左右滑动
        mousemove(event){
            var a1=document.querySelector("#ul");
            var sum=a1.childElementCount;  //li的个数
            var lis=document.querySelector("#ul>li");
            var width=lis.style.width.split("px")[0];  //li的宽度
            // 触屏所触发的事件
            var touchmovex1 =parseInt(event.changedTouches[0].clientX);
            var touchmovey1 =parseInt(event.changedTouches[0].clientY);
            if((touchmovex1-touchmovey1)>this.x){
                if(this.marginLeft<-10){
                    this.marginLeft+=5;
                    a1.style.marginLeft=this.marginLeft+"px";
                }
            }else{
                if(this.marginLeft>-((sum-2)*width)-10){ 
                    this.marginLeft-=10;
                    a1.style.marginLeft=this.marginLeft+"px";
                }
                 
            }
            this.x=touchmovex1-touchmovey1
        },
        // 轮播图
        banner(){
            var url="index_banner";
            this.axios.get(url).then(result=>{
                this.banner_img=result.data.data;
                //  console.log(this.banner_img);
            })
        },
        // 九宫格
        four(){
            var url="index_four";
            this.axios.get(url).then(result=>{
                this.four_list=result.data.data;
            })
        },
        // 新品商品列表
        index_new(){
            var url="index_new";
            this.axios.get(url).then((result)=>{
                this.product_new=result.data.data;
            })
        },
        // 人气商品列表
        index_popular(){
            var url="index_popular";
            this.axios.get(url).then((result)=>{
                this.product_popular=result.data.data;
            })
        },
         // 生日商品列表
        index_birth(){
            var url="index_birth";
            this.axios.get(url).then((result)=>{
                this.product_birth=result.data.data;
            })
        },
        // 文章列表
        index_text(){
            var url="index_text";
            this.axios.get(url).then((result)=>{
                this.product_text=result.data.data;
            })
        },
        // 通过点击图片，获取当前商品pid,跳转到商品详情页
        details_url(e){
            var pid=e.target.dataset.pid;
            this.$router.push("/details?pid="+pid)
        },
        // 通过点击购物车，获取当前商品pid，添加到当前用户的购物车中
        addCart(e){
            if(!this.uid){
                Toast("您还未登录,请先登陆");
                this.$store.state.cartCount=0;
                return;
            }else{
                var pid=e.target.dataset.pid;
                var pname=e.target.dataset.pname;
                var price=e.target.dataset.price;
                var weight=e.target.dataset.weight;
                console.log(this.uid,pid,pname,price,weight)
                var url="addcart?uid="+this.uid+"&pid="+pid+"&pname="+pname+"&price="+price+"&weight="+weight;
                this.axios.get(url).then(res=>{
                    this.$store.commit("increment");
                    Toast("添加至购物车")
                })
            }
        },
        // 通过点击文章跳转到文章详情页，显示当前文章详情
        text_details(e){
            console.log(e.target.dataset.pid)
        },
    }
}
</script>
<style>
html{
    background: #fff;
}
#d1{
    position: relative;
    z-index: 100;
}
.jiugongge{
    padding:0px 15px 0px 15px !important; 
    width: 25% !important;
}
.mycolor{
    color: #C69C6D;
}
.myfont{
    font-size: 14px;
}
.activity{
    width: 100%;
    height: 30px;
    line-height: 30px;
    display: flex;
    justify-content: space-around;
    background: #fff;
}
.activity span::before{
    content: "";
    width: 20px;
    height: 20px;
    display: inline-block;
    vertical-align: middle;
    padding-left:-10px;
    padding-bottom:10px; 
    /* background: url("../../img/new-home-icon.png") no-repeat; */
    background-position: 3px -5px;
    background-size: 80px auto;

}
.mui-grid-view.mui-grid-9 .mui-table-view-cell{
    border-right: 0 !important;
}
.app-home{
    background: #fff;
}
.app-home .mint-swipe{
    height: 200px;
}
.app-home .mint-swipe img{
    width: 100%;
}
.app-home .mui-grid-9 img{
    width: 60px;
    height: 60px;
}
.app-home .mui-grid-view.mui-grid-9{
    background: #fff;
}
.floor1{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}
.floor1>div{
    box-sizing: border-box;
    width: 30.333%;
    margin-top:5px; 
    border: 1px solid #ddd;
}
.floor1>div img{
    width: 100%;
    cursor: pointer;
}
.floor1>div p{
    width: 100%;
    height: 20px;
    font-size: 14px;
    overflow: hidden;/*溢出隐藏*/
    text-overflow: ellipsis;/*不允许换行*/
    white-space: nowrap;/*省略号*/
}
.floor1>div span{
    color: #FF5500;
}
.index_new{
    display: flex;
    background: #fff;
    justify-content: space-between;
    border-top: 1px solid #e5e5e5;
    margin: 10px;
    padding-top:10px; 
}
.index_new>p:first-child{
    width: 50px;height: 20px;
    text-align: center;
    color: #000;
    background: #f4ebe2;
    border-radius: 10px;
}
.index_new>p:last-child>a{
    font-weight: 600; 
    font-size: 13px;
    color: rgba(0,0,0,.5);
}
.mui-card-content{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 20px;
}
.mui-card{
    box-shadow: 1px 3px 15px rgba(0, 0, 0, .3) !important;
}
.cart{
    width: 45px;
    height: 45px;
    border: 1px solid transparent;
}
.index_act{
    display: flex;
    background: #fff;
    justify-content: space-between;
    border-top: 1px solid #e5e5e5;
    margin:0 10px 0 10px;
    padding-top:10px; 
    margin-bottom: 0px;
}
.index_act>p{
    width: 100px;height: 20px;
    text-align: center;
    color: #000;
    background: #f4ebe2;
    border-radius: 10px;
    padding-bottom: 0px;
}
.text{
    width: 95%;
    overflow: hidden;
    margin: 0 auto;
}
.text>ul{
    display: flex;
    width: 500%;
    list-style: none;
    padding: 0;
    margin-top: 0px; 
    margin-left: -0px;
}
.text>ul li{
    width: 231px;
    margin-right: 10px;
    box-shadow: 0 0 10px 0 rgba(0,0,0,0.3);
    color: rgba(0,0,0,.9)
}
.index_activity{
    display: flex;
    border-radius: 2px;
}
.index_activity>div:nth-child(1) h6{
    font-size:16px;
    line-height: 22px;
    margin-top: 12px;
    text-overflow: ellipsis;
    overflow: hidden;
    height: 22px;
    white-space: nowrap;
    color: #442818;
}
.index_activity>div:nth-child(1) p{
    font-size: 12px;
    color: #999;
    height: 70px;
    background: #fff;
    text-align: left;
    padding: 0 0px;
    overflow: hidden;
}
.index_activity>div:nth-child(1) span{
        font-size: 13px;
    color: #C2A185;
    float: left;
    display: block;
    padding-left: 10px;
    margin-top: 10px;
}
.text_more a{
    display: block;
    font-size: 15px;
    text-align: center;
    line-height: 170px;
    color: #442818;
}
</style>



