<template>
 <div class="app-container" style="background:#fff;">
    <!-- App.vue 组件 -->
    <!-- 1.公共顶部导航条 -->
    <div class="myheader">
         <div class="myheader_top">
            <div class="myheader_top_left">
               <img id="lala" src="img/top-icon.png" @click="header_top" width="18" height="14" alt="">
               <p style="padding-top:3px;"><img src="img/address.png" width="12" height="15" class="mycolor myfont"  alt=""><span>广州</span></p>
            </div>
            <router-link to="/index"><img src="img/logo.png" width="30" height="24" alt=""></router-link>
            <div class="myheader_top_right">
               <img src="img/header_message.png" width="20" alt="">
               <img src="img/cart.png" width="19" height="19"  @click="shopcart" alt="">
               <div>
                  {{$store.getters.optCartCount}}
               </div>
            </div>
         </div>
         <div class="myheader_1 active" >
            <div @click="index"><router-link to="/index"><img src="img/menu-home.png" width="25" height="25" alt=""></router-link><p>首页</p></div>
            <div><img src="img/menu-icon-class.png" width="25" height="25" alt=""><p>分类</p></div>
            <div @click="login">
               <img src="img/menu-icon-per.png" width="25" height="25" alt="">
               <p>个人</p>
            </div>
         </div>
    </div>
    <div class="zhanwei"></div>
    <!-- 2.容器显示其他组件 -->
    <router-view></router-view>
    <!-- footer -->
      <div class="footer">
         <div class="footer_top">
             <div>下载APP</div>
             <div>联系我们</div>
             <div>全站公告</div>
             <div>生产经营资质</div>
         </div>
         <div class="footer_bottom">
             <p>Copyright© 21Cake蛋糕官网商城 2007-2017, </p>
             <p>版权所有 京ICP备14006254号-1</p>
             <p>网站注册公司名称: 北京廿一客食品有限公司</p>
         </div>
      </div>
 </div>
</template>
<script>
export default {
      data(){
         return {
            img:true,
            product_sum:0
         }
      },
      created(){
         this.sum()
      },
      methods:{
         shopcart(){
            this.$router.push("/shopCart")
         },
         // 商品数量
         sum(){
            this.product_sum=this.$store.state.product_sum;
         },
         // 首页顶部导航栏
         header_top(e){
            if(this.img){
               e.target.src="img/menu-hide.png";
               var mm=document.querySelector(".myheader_1");
               mm.className="myheader_1"
               this.img=false;
            }else{
               e.target.src="img/top-icon.png"
               var mm=document.querySelector(".myheader_1");
               mm.className="myheader_1 active"
               this.img=true;
            } 
         },
         login(){
            var lala=document.getElementById("lala");
            lala.src="img/top-icon.png";
            var mm=document.querySelector(".myheader_1");
            mm.className="myheader_1 active"
            this.img=true;
            if(sessionStorage.getItem("uid")){
               this.$router.push("/person")
            }else{
               this.$router.push("/login")
            }
         },
         index(){
            var lala=document.getElementById("lala");
            lala.src="img/top-icon.png";
             var mm=document.querySelector(".myheader_1");
            mm.className="myheader_1 active"
            this.img=false;
            
            this.$router.push("/index")
         }
      }
}
</script>
<style>
.myheader_top_right{
   position: relative;
}
.myheader_top_right div{
   position: absolute;
   width: 15px;
   height: 15px;
   background: rgb(186,153,120);
   font-size: 13px;
   color: #fff;
   text-align: center;
   line-height: 15px;
   top: -5px;
   right: 5px;
}
.myheader{
   width: 100%;
   height: 48px !important;
   background: #fff !important;
   box-shadow: 0px 2px 4px 0px rgba(0,0,0,0.10);
   position: fixed;
   z-index: 10;
}
.myheader_top{
   display: flex;
   height: 100%;
   flex-direction: row;
   justify-content: space-between;
   align-items: center;
   padding: 10px;
   padding-top: 13px;
}
.myheader_top_left{
   display: flex;
   width: 70px;
   justify-content: space-between;
   align-items: center;
}
.myheader_top_left p{
   margin: 0;
}
.myheader_top_right{
   width: 70px;
}
.myheader_top_right img{
   margin-left:10px; 
}
.myheader_1{
   display: flex;
   justify-content: space-around;
   background: #fff !important;
   padding-top: 10px;
   z-index: 10;
}
.zhanwei{
   width: 100%;
   height: 48px;
   background: transparent;
}
.active{
   display: none;
   opacity: 0;
}
.app-container{
   overflow-x:hidden;
   background: #f6f6f6;
}

.mui-bar-tab .mui-tab-item-tao.mui-active {
    color: #007aff;
 }
.mui-bar-tab .mui-tab-item-tao {
    display: table-cell;
    overflow: hidden;
    width: 1%;
    height: 50px;
    text-align: center;
    vertical-align: middle;
    white-space: nowrap;
    text-overflow: ellipsis;
    color: #929292;
}
.mui-bar-tab .mui-tab-item-tao .mui-icon {
    top: 3px;
    width: 24px;
    height: 24px;
    padding-top: 0;
    padding-bottom: 0;
}
.mui-bar-tab .mui-tab-item-tao .mui-icon~.mui-tab-label {
   font-size:11px;
   display:block;
   overflow:hidden;
   text-overflow:ellipsis;
}
.footer{
    background: #414141;
}
.footer_top{
    display: flex;
    justify-content: space-around;
}
.footer_top div{
    width: 95px;
    height: 35px;
    text-align: center;
    font-size: 14px;
    line-height: 35px;
    color: #979797;
    border: 1px solid #979797;  
    margin: 20px 0 20px 0;
}
.footer_bottom{
    padding-bottom: 30px;
}
.footer_bottom p{
    font-size: 12px;
    color:#979797; 
    margin: 0;
    text-align: center;
}
</style>



