<template>
    <div class="app-shopcart" style="min-height:706px;">
        <div class="notice">
            <div style="width:100%;background:#e6dbce;height:40px;padding:0 15px;display:flex;item-align:center;">
                <p></p>
                <p style="font-size:12px;margin:10px 0px 0px 0px;padding:0px 8px 10px 8px;background:#d5bfa7;height:20px;color:#fff;">公告</p>
                <span style="font-size:12px;padding-top:10px;">卜壳面包，不添加的味道，马上品尝</span>
                <span style="font-size:10px;padding-top:10px;padding-left:3px;color:#BB9772;">点击查看>></span>
            </div>
        </div>
        <!-- 商品表格 -->
        <div class="shopcart_list" v-for="(item,index) of list" :key="index" style="padding:10px 0px;background:#f8f8f8;">
            <!-- 商品上半部分 -->
            <div class="product_top" style="background:#fff;">
                <img src="img/shopcart1.jpg" width="109px" height="120px" alt="">
                <!-- 图片右侧信息 -->
                <div>
                    <p class="shopcart_uname">{{item.pname}}</p>
                    <p class="shopcart_weight">规格<span>{{item.pweight}}</span></p>
                    <span class="shopcart_price">￥{{parseInt(item.price)*item.count}}.00</span>
                    <div class="shopcart_change">
                        <a class="shopcart_reduce" href="javascript:;">
                            <img src="img/-.png" alt="" width="10px" :data-pid="item.pid" :data-pweight="item.pweight" :data-price="item.price" :data-count="item.count" @click="shopcart_reduce($event)">
                        </a>
                        <span  class="shopcart_sum" style="">{{item.count}}</span>
                        <a href="javascript:;" class="shopcart_add" >
                            <img src="img/+.png" alt="" width="10px" :data-pid="item.pid" :data-pweight="item.pweight" :data-price="item.price" @click="shopcart_add($event)">
                        </a>
                    </div>
                </div>
            </div>
            <!-- 商品下半部分 -->
            <div class="product_bottom" style="background:#fff;">
                <!-- 包含餐具数量 -->
                <p class="laid_count">
                   每份含免费餐具10套 
                </p>
                <!-- 生日牌 -->
                <p class="birth_cart" :id="'pid'+item.pid" @click="birth_cart">
                    + 添加生日牌
                </p>
            </div>
        </div>
        <!-- 未登录时不显示商品 -->
        <div style="text-align:center;margin-top:100px;" :class="no_uid=1?'hide':''">
            <img src="img/cart-empty.png" alt="" width="150px">
            <p>您的还没登录，请先登录···</p>
            <router-link to="/login" style="color:#442818;font-size:12px;">去登陆>></router-link>
        </div>
        <!-- 购物车为空时显示 -->
        <div style="text-align:center;margin-top:100px;" :class="no_product==2?'hide':''">
            <img src="img/cart-empty.png" alt="" width="150px">
            <p>您的购物车还没有商品</p>
            <router-link to="/index" style="color:#442818;font-size:12px;">去购物>></router-link>
        </div>
        <!-- 底部总价格 -->
        <div class="body_footer">
            <a href="javascript:;" class="clearAll" @click="clearAll">
            </a>
            <a href="javascript:;" class="pay">去结算</a>
            <div class="pay_price">
                <div>
                    ¥
                    <span>{{All_price}}.00</span>
                    <span>商品</span>
                </div>
            </div>
        </div>
        <!-- 生日牌 -->
        <div class="birth_cart_show" :class="left_right==0?'left':''" style="background:#fff;">
            <div class="title_top">
                <img src="img/Page-back.png" @click="sel" height="20px;" alt="">
                请选择生日卡
            </div>
            <!-- 自定义 -->
            <ul>
                <li class="self_cart" :class="active==0?'selected':''" style="margin:10px 0 10px 0;">
                    <div>
                        <span>自定义</span>
                        <input  data-id="0" style="font-size:14px;" @click="sel" v-model="self_name"  type="text"  placeholder="生日牌最多只能">
                    </div>
                </li>
                <li class="self_cart" style="margin-top:10px;" data-id="1" @click="sel" :class="active==1?'selected':''">
                    生日快乐
                </li>
                <li class="self_cart" data-id="2" @click="sel" :class="active==2?'selected':''">
                    节日快乐
                </li>
                <li class="self_cart" data-id="3" @click="sel" :class="active==3?'selected':''">
                    Happy Birthday 
                </li>
                <li class="self_cart" data-id="4" @click="sel" :class="active==4?'selected':''">
                    (无)
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
import {Toast} from "mint-ui"
export default {
    data(){
        return {
            uid:0,
            list:[],
            // 购物车有没有登陆
            no_uid:1,
            // 购物车为空
            no_product:2,
            // 商品总价格
            All_price:0,
            // 商品的生日牌
            active:1,
            // 添加生日牌  通过pid
            pid:'',
            // 生日牌是否显示
            left_right:0,
            // 自定义生日牌
            self_name:''
        }
    },
    created(){
        this.uid=this.$store.state.uid;
        this.productList();
        this.sum()
        
    },
    methods:{
        // 点击选择生日牌的内容
        birth_cart(e){
            this.left_right=1;
            this.pid=e.target.id;
        },
        // 选中的生日牌
        sel(e){
            if(e.target.dataset.id==null){
                this.left_right=0;
                if(this.self_name!=null){
                    document.getElementById(this.pid).innerHTML=this.self_name;
                    this.self_name=""
                }
            }else{
                if(e.target.dataset.id!=0){
                    this.left_right=0;
                }
                this.active=e.target.dataset.id;
                document.getElementById(this.pid).innerHTML=e.target.innerHTML
            }
        },
        //商品的总价
        sum(){
            this.All_price=0;
            // 将购物车中的商品总价格汇总起来
            var arrs=document.getElementsByClassName("shopcart_price");
            for(var arr of arrs){
                this.All_price+=parseInt(arr.innerHTML.split("￥")[1])
            } 
        },
        // 增加商品的数量
        shopcart_add(e){
            var pid=e.target.dataset.pid
            var pweight=e.target.dataset.pweight;
            var price=parseInt(e.target.dataset.price);
            var url="shopcart_add?uid="+this.uid+"&pid="+pid+"&pweight="+pweight;
            this.axios.get(url).then(result=>{
                if(result.data.code==1){
                    this.productList();
                    this.sum()
                    this.All_price+=price;
                }
            })
        },
        // 减少商品的数量
        shopcart_reduce(e){
            console.log(e.target)
            var pid=e.target.dataset.pid;
            var pweight=e.target.dataset.pweight;
            var count=e.target.dataset.count;
            var price=parseInt(e.target.dataset.price);
            if(count>1){
                var url="shopcart_reduce?uid="+this.uid+"&pid="+pid+"&pweight="+pweight;
                this.axios.get(url).then(result=>{
                    if(result.data.code==1){
                        this.productList();
                        this.sum()
                        this.All_price-=price;
                    }
                })
            }else{
                var url="shopcart_delete?uid="+this.uid+"&pid="+pid+"&pweight="+pweight;
                this.axios.get(url).then(result=>{
                    if(result.data.code==1){
                        this.$store.state.cartCount-=1;
                        this.All_price=0;
                        this.productList();
                        setTimeout(()=>{
                        this.sum()
                    },500)
                    }
                })
            }
        },
        // 清除该用户所有的商品
        clearAll(){
            this.$store.state.cartCount=0;
            if(this.uid){
                this.list=[];
                var url="clearAll?uid="+this.uid;
                this.axios.get(url).then(result=>{
                    // 当用户的购物车为空时
                    if(this.uid!=0){
                        if(this.length==0){
                            this.no_product=1;
                        }else{
                            this.no_product=2;
                        }
                    }
                    Toast("购物车已清空")
                })
            }
        },
        // 通过判断是否登陆，来执行商品列表
        productList(){
            if(this.uid!=0){
                var url="productList?uid="+this.uid;
                this.axios.get(url).then(result=>{
                    this.list=result.data.data;
                    setTimeout(()=>{
                        this.sum()
                    },500)
                })
            }else{
                Toast("请登录");
                this.$store.state.cartCount=0;
                this.no_uid=0;
            }
        }
    }
}
</script>
<style>
.birth_cart_show{
    width: 100%;
    height: 100%;
    background: #fff;
    position: fixed;
    top: 0;
    left: 0%;
    transition: .3s;
    z-index: 10000000;
}
.title_top{
    width: 100%;
    height: 40px;
    text-align: center;
    font-size: 15px;
    color:#442818; 
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 20px;
    box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.10);
}
.self_cart{
    width:100%;
    color:#442818;
    border-left:0;
    border-right:0;
    background:#f8f8f8;
    padding-left:40px;
    font-size:14px;
    height: 40px;
    display:flex;
    align-items: center;
    border-bottom: 1px solid #fff;
}
.self_cart>div{
    font-size:14px;
    width:100%;
}
.self_cart>div>span{
    width:20%;
    padding-top:10px;
}
.self_cart>div>input{
    border:0;
    width:88%;
    margin:0;
    background:#f8f8f8;
}
.selected{
    /* background:url(img/list_check_3x.png) no-repeat 10px; */
    background-size:20px;
}
.left{
    left: 100%;
}
.title_top>img{
    margin-right:110px; 
}
.self_write{   
}
.body_footer{
    height: 44px;
    overflow: hidden;
    background: #fff;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 1000000;
    box-shadow: 0px -2px 4px 0px rgba(0,0,0,0.10);
}
.clearAll{
    display: block;
    float: left;
    height: 44px;
    width: 44px;
    /* background: url(img/cart-order-delete.png) no-repeat center center; */
    background-size: 14px auto;
}
.pay{
    display: block;
    float: right;
    height: 44px;
    width: 96px;
    line-height: 44px;
    text-align: center;
    color: #fff;
    background: #442818;
    font-size: 12px;
    margin-left: 10px;
}
.pay_price{
    float: right;
    margin-right: 12px;
    margin: 0;
    padding: 0;
    font-size: 12px;
    color: #442818;
}
.pay_price>div:first-child{
    float: left;
    height: 44px;
    text-align: right;
    margin: 0;
    padding: 0;
    font-size: 12px;
    color: #442818;
}
.pay_price>div span:first-child{
    margin-top: 5px;
    display: inline-block;
    font-size: 16px;
    font-weight: bold;
    line-height: 16px;
    text-align: right;
    color: #442818;
}
.pay_price>div span:last-child{
    display: block;
    color: #707070;
    font-size: 10px;
    line-height: 16px;
    text-align: right;
}
.product_top{
    display: flex;
    height:120px;
    padding: 0 15px;
}
.product_top>div:first-child{
    width:100%;height:126px;
}
.shopcart_title{
    margin:0 !important;
    margin-top:15px !important;
    padding-left:10px !important;
    color:#442818;
    font-size: 13px;
}
.shopcart_uname{
    margin:0;
    margin-top:15px !important;
    padding-left:10px !important;
    color:#442818 !important;
    font-size: 13px !important;
}
.shopcart_weight{
    margin:0;
    padding-left:10px;
    font-size: 11px;
}
.shopcart_price{
    padding-left:10px;
    display:inline-block;
    /* margin-top:10px; */
    color: #969696;
    font-size:11px;
}
.shopcart_change{
    position:relative;
    left:150px;
    top:-20px;
}
.shopcart_add{
    display:inline-block;
    width:26px;height:26px;
    text-align:center;
    background:#f0f0f0;
}
.shopcart_sum{
    display:inline-block;
    width:26px;height:26px;
    background:#f7f7f7;
    text-align:center;
    font-size:12px; 
    line-height: 26px;
    color: #969696;
}
.shopcart_reduce{
    display:inline-block;
    width:26px;height:26px;
    text-align:center;
    background:#f0f0f0;
}
.laid_count{
    margin: 0;
    padding: 0;
    margin: 0 20px;
    padding-left:55px; 
    height: 46px;
    line-height: 46px;
    font-size: 12px;
    color: #442818;
    /* background: url(img/cart-laid.png) no-repeat left; */
    background-size: 46px 46px;
}
.birth_cart{
    margin: 0;
    padding: 0;
    margin: 0 20px;
    padding-left:80px; 
    height: 46px;
    line-height: 46px;
    font-size: 12px;
    color: #442818;
    /* background: url(img/cart-birthday.jpg) no-repeat left,url(img/go-right.png) right center no-repeat; */
    background-size: 66px 30px ,8px 12px;
}
</style>



